@use '../util' as *;

.footer {
  background: var(--cyan);
  display: grid;
  gap: rem(40);
  place-items: center;
  text-align: center;
  padding-block: rem(50);

  &__menu {
    display: flex;
    gap: rem(40);
    margin-bottom: rem(30);

    a {
      color: var(--dark-med-cyan);
      transition: var(--transition-3s) ease-in-out;

      &:hover {
        color: var(--white);
      }
    }
  }

  .socials {
    display: flex;
    gap: rem(22);
    font-size: rem(20);

    a {
      transition: var(--transition-3s) ease-in-out;
      color: var(--dark-med-cyan);
     
      &:hover {
        color: var(--white);
      }
    }
  }
}