@use '../util' as *;

.testimonials {
  padding-block: rem(50);
  display: grid;
  gap: rem(50);
  text-align: center;

  h1 {
    font-family: var(--ff-fraunces);
    font-weight: 900;
    color: var(--grayish-blue);
    letter-spacing: rem(4);
    text-transform: uppercase;
    font-size: rem(16);
  }

  .testimonials__item {
    display: grid;
    place-items: center;

    .item__img {
      width: 57px;
      height: 57px;
      margin-bottom: rem(30);

      img {
        border-radius: 50%;
      }
    }

    p {
      margin-bottom: rem(30);
      color: var(--vdark-grayish-blue);
    }

    h4 {
      color: var(--vdark-desat-blue);
      font-family: var(--ff-fraunces);
      font-weight: 900;
      padding-bottom: rem(8);
    }

    span {
      color: var(--grayish-blue);
      font-size: rem(13);
    }
  }
}