@use '../util' as *;

.header {
  background: url(../../../img/mobile/image-header.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;

  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: rem(25);
    margin-bottom: rem(70);
    position: relative;

    .logo {
      height: 24px;
    }

    .menu-icon {
      width: 20px;
      background: none;
      border: none;
      cursor: pointer;
      transition: var(--transition-3s) ease-in-out;
      
      &:hover {
        transform: rotate(90deg);
      }
    }

    .menu {
      display: grid;
      position: absolute;
      top: 95px;
      background: var(--white);
      left: 0;
      right: 0;
      text-align: center;
      padding-top: rem(20);
      padding-bottom: rem(40);
      transform: rotateX(-90deg);
      opacity: .25;
      transform-origin: top center;
      transition: all 120ms ease-in;

      a {
        display: inline-block;
        padding-block: rem(20);
        color: var(--dark-grayish-blue);
        font-size: rem(18);
        transition: var(--transition-3s) ease-in-out;

        &:hover {
          color: var(--vdark-desat-blue);
        }
      }

      .contact {
        background: var(--yellow);
        padding: rem(16) rem(30);
        border-radius: rem(32);
        color: var(--vdark-desat-blue);
        font-family: var(--ff-fraunces);
        font-weight: 900;
        text-transform: uppercase;
        font-size: rem(16);
        transition: var(--transition-3s) ease-in-out;

        &:hover {
          color: var(--white);
          background: var(--prim-soft-red);
        }
      }

      &::before {
        content: '';
        width: 30px;
        height: 30px;
        position: absolute;
        top: -30px;
        right: 0;
        border: 15px solid var(--white);
        border-top: 15px solid transparent;
        border-left: 15px solid transparent;
      }

      &.ativo {
        transform: rotateX(0);
        opacity: 1;
        transition-timing-function: ease-out;
      }
    }
  }

  .hero {
    text-align: center;
    display: grid;
    place-items: center;

    h1 {
      font-family: var(--ff-fraunces);
      font-weight: 900;
      text-transform: uppercase;
      color: var(--white);
      font-size: rem(38.4);
      line-height: initial;
      letter-spacing: rem(8);
      margin-bottom: rem(44);
    }

    .arrow {
      width: 36px;
      height: 114px;
      margin-bottom: rem(104);
    }
  }
}