*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  min-height: 100vh;
  line-height: 1.5;
  font-family: var(--ff-barlow);
}

.links {
  color: var(--vdark-desat-blue);
  font-family: var(--ff-fraunces);
  text-transform: uppercase;
  font-weight: 900;
  position: relative;
  z-index: 2;
  padding-inline: 10px;
}
.links::after {
  content: "";
  width: 100%;
  height: 8px;
  border-radius: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  transition: var(--transition-3s) ease-in-out;
}

.links--yellow::after {
  background: var(--v-light-yellow);
}

.links--yellow:hover::after {
  background: var(--yellow);
}

.links--red::after {
  background: var(--v-light-red);
}

.links--red:hover::after {
  background: var(--prim-soft-red);
}

.no-scroll {
  overflow-y: hidden;
}

.container {
  width: min(90%, 75rem);
  margin-inline: auto;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: #000;
  --prim-soft-red: hsl(7, 99%, 70%);
  --v-light-red: hsl(7, 91%, 91%);
  --yellow: hsl(51, 100%, 49%);
  --v-light-yellow: hsl(51, 95%, 83%);
  --dark-desat-cyan: hsl(167, 40%, 24%);
  --dark-blue: hsl(198, 62%, 26%);
  --cyan: hsl(168, 57%, 72%);
  --dark-med-cyan: hsl(168, 34%, 41%);
  --vdark-desat-blue: hsl(212, 27%, 19%);
  --vdark-grayish-blue: hsl(213, 9%, 39%);
  --dark-grayish-blue: hsl(232, 10%, 55%);
  --grayish-blue: hsl(210, 4%, 67%);
  --ff-barlow: "Barlow", sans-serif;
  --ff-fraunces: "Fraunces", serif;
  --transition-3s: all .3s;
}

.header {
  background: url(../../../img/mobile/image-header.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
}
.header .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1.5625rem;
  margin-bottom: 4.375rem;
  position: relative;
}
.header .nav .logo {
  height: 24px;
}
.header .nav .menu-icon {
  width: 20px;
  background: none;
  border: none;
  cursor: pointer;
  transition: var(--transition-3s) ease-in-out;
}
.header .nav .menu-icon:hover {
  transform: rotate(90deg);
}
.header .nav .menu {
  display: grid;
  position: absolute;
  top: 95px;
  background: var(--white);
  left: 0;
  right: 0;
  text-align: center;
  padding-top: 1.25rem;
  padding-bottom: 2.5rem;
  transform: rotateX(-90deg);
  opacity: 0.25;
  transform-origin: top center;
  transition: all 120ms ease-in;
}
.header .nav .menu a {
  display: inline-block;
  padding-block: 1.25rem;
  color: var(--dark-grayish-blue);
  font-size: 1.125rem;
  transition: var(--transition-3s) ease-in-out;
}
.header .nav .menu a:hover {
  color: var(--vdark-desat-blue);
}
.header .nav .menu .contact {
  background: var(--yellow);
  padding: 1rem 1.875rem;
  border-radius: 2rem;
  color: var(--vdark-desat-blue);
  font-family: var(--ff-fraunces);
  font-weight: 900;
  text-transform: uppercase;
  font-size: 1rem;
  transition: var(--transition-3s) ease-in-out;
}
.header .nav .menu .contact:hover {
  color: var(--white);
  background: var(--prim-soft-red);
}
.header .nav .menu::before {
  content: "";
  width: 30px;
  height: 30px;
  position: absolute;
  top: -30px;
  right: 0;
  border: 15px solid var(--white);
  border-top: 15px solid transparent;
  border-left: 15px solid transparent;
}
.header .nav .menu.ativo {
  transform: rotateX(0);
  opacity: 1;
  transition-timing-function: ease-out;
}
.header .hero {
  text-align: center;
  display: grid;
  place-items: center;
}
.header .hero h1 {
  font-family: var(--ff-fraunces);
  font-weight: 900;
  text-transform: uppercase;
  color: var(--white);
  font-size: 2.4rem;
  line-height: initial;
  letter-spacing: 0.5rem;
  margin-bottom: 2.75rem;
}
.header .hero .arrow {
  width: 36px;
  height: 114px;
  margin-bottom: 6.5rem;
}

.about {
  text-align: center;
}
.about h1 {
  font-size: 1.875rem;
  font-weight: 900;
  font-family: var(--ff-fraunces);
  color: var(--vdark-desat-blue);
  margin-bottom: 1.5625rem;
}
.about p {
  color: var(--vdark-grayish-blue);
  line-height: 1.6875rem;
  margin-bottom: 1.875rem;
}
.about .pd-block {
  padding-block: 3.125rem;
  padding-inline: 1.75rem;
}

.services {
  text-align: center;
  display: grid;
  grid-template-rows: repeat(2, 570px);
}
.services h1 {
  font-family: var(--ff-fraunces);
  font-weight: 900;
  font-size: 1.5rem;
}
.services .graphic-design {
  background: url(../../../img/mobile/image-graphic-design.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  display: grid;
  align-items: end;
  padding-block: 3.125rem;
  padding-inline: 1.25rem;
}
.services .graphic-design__info {
  display: grid;
  gap: 25px;
}
.services .graphic-design__info h1, .services .graphic-design__info p {
  color: var(--dark-desat-cyan);
}
.services .photography {
  background: url(../../../img/mobile/image-photography.jpg) no-repeat;
  background-size: cover;
  background-position: top;
  display: grid;
  align-items: end;
  padding-block: 3.125rem;
  padding-inline: 1.25rem;
}
.services .photography__info {
  display: grid;
  gap: 25px;
}
.services .photography__info h1, .services .photography__info p {
  color: var(--dark-blue);
}

.testimonials {
  padding-block: 3.125rem;
  display: grid;
  gap: 3.125rem;
  text-align: center;
}
.testimonials h1 {
  font-family: var(--ff-fraunces);
  font-weight: 900;
  color: var(--grayish-blue);
  letter-spacing: 0.25rem;
  text-transform: uppercase;
  font-size: 1rem;
}
.testimonials .testimonials__item {
  display: grid;
  place-items: center;
}
.testimonials .testimonials__item .item__img {
  width: 57px;
  height: 57px;
  margin-bottom: 1.875rem;
}
.testimonials .testimonials__item .item__img img {
  border-radius: 50%;
}
.testimonials .testimonials__item p {
  margin-bottom: 1.875rem;
  color: var(--vdark-grayish-blue);
}
.testimonials .testimonials__item h4 {
  color: var(--vdark-desat-blue);
  font-family: var(--ff-fraunces);
  font-weight: 900;
  padding-bottom: 0.5rem;
}
.testimonials .testimonials__item span {
  color: var(--grayish-blue);
  font-size: 0.8125rem;
}

.projects {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.footer {
  background: var(--cyan);
  display: grid;
  gap: 2.5rem;
  place-items: center;
  text-align: center;
  padding-block: 3.125rem;
}
.footer__menu {
  display: flex;
  gap: 2.5rem;
  margin-bottom: 1.875rem;
}
.footer__menu a {
  color: var(--dark-med-cyan);
  transition: var(--transition-3s) ease-in-out;
}
.footer__menu a:hover {
  color: var(--white);
}
.footer .socials {
  display: flex;
  gap: 1.375rem;
  font-size: 1.25rem;
}
.footer .socials a {
  transition: var(--transition-3s) ease-in-out;
  color: var(--dark-med-cyan);
}
.footer .socials a:hover {
  color: var(--white);
}