@use '../util' as *;

.about {
  text-align: center;

  h1 {
    font-size: rem(30);
    font-weight: 900;
    font-family: var(--ff-fraunces);
    color: var(--vdark-desat-blue);
    margin-bottom: rem(25);
  }

  p {
    color: var(--vdark-grayish-blue);
    line-height: rem(27);
    margin-bottom: rem(30);
  }

  .pd-block {
    padding-block: rem(50);
    padding-inline: rem(28);
  }
}