:root {
  --white: hsl(0, 0%, 100%);
  --black: #000;
  --prim-soft-red: hsl(7, 99%, 70%);
  --v-light-red: hsl(7, 91%, 91%);
  --yellow: hsl(51, 100%, 49%);
  --v-light-yellow: hsl(51, 95%, 83%);
  --dark-desat-cyan: hsl(167, 40%, 24%);
  --dark-blue: hsl(198, 62%, 26%);
  --cyan: hsl(168, 57%, 72%);
  --dark-med-cyan: hsl(168, 34%, 41%);
  --vdark-desat-blue: hsl(212, 27%, 19%);
  --vdark-grayish-blue: hsl(213, 9%, 39%);
  --dark-grayish-blue: hsl(232, 10%, 55%);
  --grayish-blue: hsl(210, 4%, 67%);
  --ff-barlow: 'Barlow', sans-serif;;
  --ff-fraunces: 'Fraunces', serif;;
  --transition-3s: all .3s;
}