@use '../util' as *;

.services {
  text-align: center;
  display: grid;
  grid-template-rows: repeat(2, 570px);

  h1 {
    font-family: var(--ff-fraunces);
    font-weight: 900;
    font-size: rem(24);
  }

  .graphic-design {
    background: url(../../../img/mobile/image-graphic-design.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    // min-height: 570px;
    display: grid;
    align-items: end;
    padding-block: rem(50);
    padding-inline: rem(20);

    &__info {
      display: grid;
      gap: 25px;

      h1, p {
        color: var(--dark-desat-cyan);
      }
    }
  }

  .photography {
    background: url(../../../img/mobile/image-photography.jpg) no-repeat;
    background-size: cover;
    background-position: top;
    // min-height: 570px;
    display: grid;
    align-items: end;
    padding-block: rem(50);
    padding-inline: rem(20);

    &__info {
      display: grid;
      gap: 25px;

      h1, p {
        color: var(--dark-blue);
      }
    }
  }
}