*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  min-height: 100vh;
  line-height: 1.5;
  font-family: var(--ff-barlow);
}

.links {
  color: var(--vdark-desat-blue);
  font-family: var(--ff-fraunces);
  text-transform: uppercase;
  font-weight: 900;
  position: relative;
  z-index: 2;
  padding-inline: 10px;
  
  &::after {
    content: '';
    width: 100%;
    height: 8px;
    border-radius: 4px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
    transition: var(--transition-3s) ease-in-out;
  }
}

.links--yellow::after{
  background: var(--v-light-yellow);
}

.links--yellow:hover::after{
  background: var(--yellow);
}

.links--red::after{
  background: var(--v-light-red);
}

.links--red:hover::after{
  background: var(--prim-soft-red);
}

.no-scroll {
  overflow-y: hidden;
}